'use client';

import { useEffect, useState } from 'react';
import { useAuth } from '@/context/AuthContext';
import { useRouter } from 'next/navigation';
import Link from 'next/link';
import Loading from '@/components/Loading';
import DoctorSidebar from '@/components/DoctorSidebar';
import api from '@/lib/api';
import { FiCalendar, FiFileText, FiClock, FiUsers, FiUser } from 'react-icons/fi';
import { format, parseISO } from 'date-fns';

export default function DoctorDashboardPage() {
  const { user, loading: authLoading, logout } = useAuth();
  const router = useRouter();
  const [stats, setStats] = useState<any>(null);
  const [doctorProfile, setDoctorProfile] = useState<any>(null);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    if (!authLoading && !user) {
      router.push('/login');
      return;
    }
    if (user && user.role !== 'doctor') {
      // Redirect to appropriate dashboard
      if (user.role === 'admin') {
        router.push('/admin/dashboard');
      } else if (user.role === 'patient') {
        router.push('/patient/dashboard');
      } else {
        router.push('/dashboard');
      }
      return;
    }
    if (user && user.role === 'doctor') {
      checkDoctorProfile();
    }
  }, [user, authLoading, router]);

  const checkDoctorProfile = async () => {
    try {
      const response = await api.get('/doctors/profile/me');
      const doctor = response.data.doctor;
      if (!doctor || doctor.status === 'pending') {
        // Redirect to complete profile if not completed or pending
        router.push('/doctor/complete-profile');
        return;
      } else if (doctor.status === 'approved') {
        setDoctorProfile(doctor);
        fetchDashboardData();
      }
    } catch (error) {
      // If no profile exists, redirect to complete profile
      router.push('/doctor/complete-profile');
    }
  };

  const fetchDashboardData = async () => {
    try {
      setLoading(true);
      const appointments = await api.get('/appointments?limit=5');
      const prescriptions = await api.get('/prescriptions?limit=5');
      setStats({
        appointments: appointments.data.appointments || [],
        prescriptions: prescriptions.data.prescriptions || [],
      });
    } catch (error) {
      console.error('Error fetching dashboard data:', error);
      setStats({
        appointments: [],
        prescriptions: [],
      });
    } finally {
      setLoading(false);
    }
  };

  if (authLoading || loading) {
    return <Loading />;
  }

  if (!user || user.role !== 'doctor') {
    return null;
  }

  return (
    <div className="flex min-h-screen bg-gray-50 dark:bg-gray-900 transition-colors">
      <DoctorSidebar user={user} logout={logout} />
      <main className="ml-64 flex-1 transition-all duration-300 bg-gray-50 dark:bg-gray-900">
        <div className="p-8">
          <div className="mb-8">
            <h1 className="text-3xl font-bold text-gray-800 dark:text-gray-100 mb-2">
              Welcome back, Dr. {user.name}!
            </h1>
            <p className="text-gray-600 dark:text-gray-400">Here's an overview of your schedule</p>
          </div>

          {/* Stats Cards */}
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6 mb-8">
            <div className="bg-white dark:bg-gray-800 rounded-xl shadow-md p-6 border border-gray-100 dark:border-gray-700 transition-colors">
              <div className="flex items-center justify-between">
                <div>
                  <p className="text-gray-600 dark:text-gray-400 text-sm">Today's Appointments</p>
                  <p className="text-3xl font-bold text-teal-600 dark:text-teal-400 mt-2">
                    {stats?.appointments?.filter((a: any) => {
                      const apptDate = format(parseISO(a.appointmentDate), 'yyyy-MM-dd');
                      const today = format(new Date(), 'yyyy-MM-dd');
                      return apptDate === today;
                    }).length || 0}
                  </p>
                </div>
                <FiCalendar className="text-4xl text-teal-600 dark:text-teal-400" />
              </div>
            </div>
            <div className="bg-white dark:bg-gray-800 rounded-xl shadow-md p-6 border border-gray-100 dark:border-gray-700 transition-colors">
              <div className="flex items-center justify-between">
                <div>
                  <p className="text-gray-600 dark:text-gray-400 text-sm">Total Appointments</p>
                  <p className="text-3xl font-bold text-teal-600 dark:text-teal-400 mt-2">
                    {stats?.appointments?.length || 0}
                  </p>
                </div>
                <FiClock className="text-4xl text-teal-600 dark:text-teal-400" />
              </div>
            </div>
            <div className="bg-white dark:bg-gray-800 rounded-xl shadow-md p-6 border border-gray-100 dark:border-gray-700 transition-colors">
              <div className="flex items-center justify-between">
                <div>
                  <p className="text-gray-600 dark:text-gray-400 text-sm">Prescriptions</p>
                  <p className="text-3xl font-bold text-indigo-600 dark:text-indigo-400 mt-2">
                    {stats?.prescriptions?.length || 0}
                  </p>
                </div>
                <FiFileText className="text-4xl text-indigo-600 dark:text-indigo-400" />
              </div>
            </div>
            <div className="bg-white dark:bg-gray-800 rounded-xl shadow-md p-6 border border-gray-100 dark:border-gray-700 transition-colors">
              <div className="flex items-center justify-between">
                <div>
                  <p className="text-gray-600 dark:text-gray-400 text-sm">Total Patients</p>
                  <p className="text-3xl font-bold text-blue-600 dark:text-blue-400 mt-2">
                    {stats?.appointments ? [...new Set(stats.appointments.map((a: any) => a.patientId))].length : 0}
                  </p>
                </div>
                <FiUsers className="text-4xl text-blue-600 dark:text-blue-400" />
              </div>
            </div>
          </div>

          {/* Recent Appointments */}
          <div className="bg-white dark:bg-gray-800 rounded-xl shadow-md p-6 mb-8 border border-gray-100 dark:border-gray-700 transition-colors">
            <div className="flex items-center justify-between mb-6">
              <h2 className="text-xl font-semibold text-gray-800 dark:text-gray-100">Today's Appointments</h2>
              <Link
                href="/doctor/appointments"
                className="text-teal-600 dark:text-teal-400 hover:text-teal-700 dark:hover:text-teal-300 text-sm font-medium transition-colors"
              >
                View All â†’
              </Link>
            </div>
            {stats?.appointments && stats.appointments.length > 0 ? (
              <div className="space-y-4">
                {stats.appointments
                  .filter((a: any) => {
                    const apptDate = format(parseISO(a.appointmentDate), 'yyyy-MM-dd');
                    const today = format(new Date(), 'yyyy-MM-dd');
                    return apptDate === today;
                  })
                  .slice(0, 5)
                  .map((appt: any) => (
                    <div
                      key={appt.id}
                      className="border border-gray-200 dark:border-gray-700 rounded-lg p-4 hover:shadow-md dark:hover:shadow-lg transition-all bg-gray-50 dark:bg-gray-700"
                    >
                      <div className="flex justify-between items-start">
                        <div className="flex-1">
                          <p className="font-semibold text-gray-800 dark:text-gray-100 text-lg">
                            {appt.patient?.name || 'Patient'}
                          </p>
                          <p className="text-sm text-gray-600 dark:text-gray-400 mt-2">
                            {format(parseISO(appt.appointmentDate), 'MM/dd/yyyy')} at {appt.appointmentTime}
                          </p>
                          {appt.reason && (
                            <p className="text-sm text-gray-700 dark:text-gray-300 mt-2 font-medium">{appt.reason}</p>
                          )}
                        </div>
                        <span
                          className={`px-3 py-1 rounded-full text-xs font-semibold ${
                            appt.status === 'confirmed'
                              ? 'bg-green-100 dark:bg-green-900 text-green-800 dark:text-green-200'
                              : appt.status === 'pending'
                              ? 'bg-yellow-100 dark:bg-yellow-900 text-yellow-800 dark:text-yellow-200'
                              : appt.status === 'completed'
                              ? 'bg-blue-100 dark:bg-blue-900 text-blue-800 dark:text-blue-200'
                              : 'bg-red-100 dark:bg-red-900 text-red-800 dark:text-red-200'
                          }`}
                        >
                          {appt.status}
                        </span>
                      </div>
                    </div>
                  ))}
              </div>
            ) : (
              <div className="text-center py-8">
                <FiCalendar className="text-5xl text-gray-300 dark:text-gray-600 mx-auto mb-4" />
                <p className="text-gray-500 dark:text-gray-400 text-lg">No appointments for today</p>
              </div>
            )}
          </div>

          {/* Quick Links */}
          <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
            <Link
              href="/doctor/appointments"
              className="bg-white dark:bg-gray-800 rounded-xl shadow-md p-6 border border-gray-100 dark:border-gray-700 hover:shadow-lg transition-all flex items-center gap-4"
            >
              <div className="w-12 h-12 rounded-full bg-teal-100 dark:bg-teal-900 flex items-center justify-center">
                <FiCalendar className="text-2xl text-teal-600 dark:text-teal-400" />
              </div>
              <div>
                <h3 className="font-semibold text-gray-800 dark:text-gray-100">My Appointments</h3>
                <p className="text-sm text-gray-600 dark:text-gray-400">View and manage appointments</p>
              </div>
            </Link>
            <Link
              href="/doctor/prescriptions"
              className="bg-white dark:bg-gray-800 rounded-xl shadow-md p-6 border border-gray-100 dark:border-gray-700 hover:shadow-lg transition-all flex items-center gap-4"
            >
              <div className="w-12 h-12 rounded-full bg-indigo-100 dark:bg-indigo-900 flex items-center justify-center">
                <FiFileText className="text-2xl text-indigo-600 dark:text-indigo-400" />
              </div>
              <div>
                <h3 className="font-semibold text-gray-800 dark:text-gray-100">Prescriptions</h3>
                <p className="text-sm text-gray-600 dark:text-gray-400">Create and manage prescriptions</p>
              </div>
            </Link>
            <Link
              href="/doctor/profile"
              className="bg-white dark:bg-gray-800 rounded-xl shadow-md p-6 border border-gray-100 dark:border-gray-700 hover:shadow-lg transition-all flex items-center gap-4"
            >
              <div className="w-12 h-12 rounded-full bg-teal-100 dark:bg-teal-900 flex items-center justify-center">
                <FiUser className="text-2xl text-teal-600 dark:text-teal-400" />
              </div>
              <div>
                <h3 className="font-semibold text-gray-800 dark:text-gray-100">My Profile</h3>
                <p className="text-sm text-gray-600 dark:text-gray-400">View and edit profile</p>
              </div>
            </Link>
          </div>
        </div>
      </main>
    </div>
  );
}



